.sidebar {
    flex: 0.1;
    padding: 1rem 1rem;
    align-items: center;
}
.sidebar__twitter-icon {
    font-size: 2.5rem !important;
    color: var(--twitter-primary);
    padding-left: 0 1rem;
}
.sidebar__tweet-btn {
    background-color: var(--twitter-primary) !important;
    color: var(--white) !important;
    text-transform: capitalize !important;
    border-radius: 33px !important;
    width: 90% !important;
    padding: 0.5rem 0 !important;
    font-size: 1.1rem !important;
    border: none !important;
}
.sidebar__tweet-btn:hover {
    background-color: var(--twitter-primary-2) !important;
}
